<template>
  <div class="editbookmsg" v-show="showControl">
    <el-dialog title="更新信息" @close="close" :visible.sync="showControl" :show-close="false" center>
      <el-form class="myFrom" ref="myForm" :model="book">
        <el-form-item label="封面" prop="author">
          <div class="demo-image__preview">
            <el-image style="width: 100px; height: 120px" :src="book.avatar">
              <div slot="placeholder" class="image-slot">
                <i class="el-icon-loading"></i>
              </div>
              <div slot="error" class="image-slot">
                <i class="el-icon-picture-outline"></i>
              </div>
            </el-image>
            <input
              class="selectfile"
              type="file"
              ref="uploadAva"
              name="avatar"
              @change="uploadAvatar"
              alt="点击上传文件"
            />
          </div>
        </el-form-item>
        <el-form-item label="作者" prop="author">
          <el-input placeholder="作者" v-model="book.author" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="书名" prop="bookname">
          <el-input placeholder="书名" v-model="book.bookname" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="简介" prop="descript">
          <el-input
            placeholder="简介"
            type="textarea"
            :rows="4"
            v-model="book.descript"
            autocomplete="off"
          ></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="update">提 交</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { getbookbyid, uploadAvatar, updatebookmsg } from "@/api/index.js";
export default {
  data() {
    return {
      showControl: false,
      book: {},
      avatarMimes: [
        "image/jpeg",
        "image/pjpeg",
        "image/jpeg",
        "image/pjpeg",
        "image/jpeg",
        "image/pjpeg",
        "image/png",
        "image/x-png",
        "image/gif"
      ]
    };
  },
  created() {
    this.$Buse.$on("editbook", bookid => {
      this.showControl = true;
      this.$nextTick(() => {
        this.getmsg(bookid);
      });
    });
  },
  methods: {
    getmsg(bookId) {
      let params = { bookId };
      getbookbyid(params).then(res => {
        if (res.data.code === 200) {
          this.book = res.data.data;
        }
      });
    },
    uploadAvatar() {
      let avatar = this.$refs["uploadAva"].files[0];
      if (!this.avatarMimes.includes(avatar.type)) {
        return this.$notify({
          title: "错误",
          message: "请选择图片文件！",
          iconClass: "el-icon-warning-outline",
          duration: 2500
        });
      }
      let params = new FormData();
      params.append("avatar", avatar);
      uploadAvatar(params).then(res => {
        if (res.data.code === 200) {
          this.book.avatar = res.data.data.avatar;
        } else {
          this.$notify({
            title: "错误",
            message: res.data.msg,
            iconClass: "el-icon-warning-outline",
            duration: 2500
          });
        }
      });
    },
    update() {
      let params = this.book;
      updatebookmsg(params).then(res => {
        this.showControl = false;
        if (res.data.code === 200) {
          this.$notify({
            title: "成功",
            message: res.data.msg,
            iconClass: "el-icon-check",
            duration: 2500
          });
          this.$parent.getBookList();
        }
      });
    },
    close() {
      this.showControl = false;
      this.book = {};
    }
  }
};
</script>

<style scoped>
.selectfile {
  width: 100px;
  height: 120px;
  position: absolute;
  margin-left: -100px;
  opacity: 0;
}
.selectfile:focus {
  outline: none;
}
.editbookmsg /deep/ .el-dialog {
  max-width: 500px;
  width: 90%;
}
.editbookmsg /deep/ .el-form .el-form-item__label {
  width: 50px;
}
.el-input,
.el-textarea {
  max-width: 300px;
}
.el-button {
  background: rgb(255, 220, 220);
  border: none;
  outline: none;
}
.el-button:hover {
  border: none;
  outline: none;
  background: rgb(255, 200, 200);
}
.editbookmsg /deep/ .el-input__inner.el-input__inner:focus,
.editbookmsg /deep/ .el-textarea__inner:focus {
  border-color: rgb(255, 220, 220);
}
</style>