<template>
  <div class="hello">
    <div class="lunbo">
      <el-carousel indicator-position="outside">
        <el-carousel-item v-for="item in 4" :key="item">
          <div class="item">
          </div>
        </el-carousel-item>
      </el-carousel>
    </div>
    <div class="books" v-if="booklist.length!==0">
      <div class="book-item" v-for="book in booklist" :key="book.bookId">
        <el-image
          style="width: 80px; height: 100px;"
          :src="book.avatar"
          :preview-src-list="[book.avatar]"
          fit="fill"
        >
          <div slot="placeholder" class="image-slot">
            <i class="el-icon-loading"></i>
          </div>
        </el-image>
        <h3 class="bookname" @click.prevent.stop="readBook(book.bookId)">{{book.bookname}}</h3>
        <h5 class="author" @click.prevent.stop="readBook(book.bookId)">{{book.author}}</h5>
        <div class="control">
          <span
            class="el-icon-edit-outline icon"
            style="color:#409EFF;"
            @click.prevent.stop="editmsg(book.bookId)"
          ></span>
          <el-tooltip class="item" effect="dark" content="继续写书" placement="bottom">
            <span
              class="el-icon-edit icon"
              style="color:#409EFF;"
              @click.prevent.stop="writebook(book.bookId)"
            ></span>
          </el-tooltip>
          <el-tooltip class="item" effect="dark" content="删除书籍" placement="bottom">
            <span
              class="el-icon-delete icon"
              style="color:#F56C6C;"
              @click.prevent.stop="deletebook(book.bookname,book.bookId)"
            ></span>
          </el-tooltip>
        </div>
        <span class="descript" @click.prevent.stop="readBook(book.bookId)">{{book.descript}}</span>
        <span class="created_time">{{book.created_time | formatTimeAgo}}</span>
      </div>
    </div>
    <Editbook />
  </div>
</template>

<script>
import { getbooklist, deletebook } from "@/api/index.js";
import Editbook from "@/components/editbookmsg";
export default {
  name: "HelloWorld",
  data() {
    return {
      booklist: [],
      EditbookId: "11",
      words: [
        { first: "遇事不决", last: "可问春风" },
        { first: "纤云弄巧", last: "银汉迢迢暗渡" },
        { first: "与善人居，如芝兰入室", last: "久自芳矣" },
        { first: "人如花木", last: "皆向阳而生" }
      ]
    };
  },
  created() {
    getbooklist().then(res => {
      if (res.data.code === 200) {
        // this.$notify({
        //   title: "成功",
        //   message: res.data.msg,
        //   iconClass: "el-icon-check",
        //   duration: 2500,
        //   showclose: false
        // });
        this.booklist = res.data.data;
      }
    });
  },
  components: { Editbook },
  methods: {
    readBook(bookid) {
      this.$router.push({ name: "helloreading", params: { bookId: bookid } });
    },
    getBookList() {
      getbooklist().then(res => {
        if (res.data.code === 200) {
          this.booklist = res.data.data;
          console.log(this.booklist)
        }
      });
    },
    editmsg(bookId) {
      this.$Buse.$emit("editbook", bookId);
    },
    writebook(bookId) {
      this.$router.push({ name: "hellowritebook", params: { bookId } });
    },
    tip() {
      console.log(1111);
    },
    deletebook(bookname, bookId) {
      let note = this.$notify({
        title: "提示",
        message: `<p class="notifymsg">听说你丢掉 <span style="color:#000;font-size:13px">《${bookname}》<span/>？</p>
           <el-link style="color:#F56C6C;cursor: pointer;">点击此处即可删除本书籍</el-link><br/>
           <el-link style="float:right;">3s后自动关闭</el-link>`,
        duration: 2500,
        iconClass: "el-icon-question",
        dangerouslyUseHTMLString: true,
        onClick: () => {
          deletebook({ bookId }).then(res => {
            note.close();
            if (res.data.code === 200) {
              this.booklist = this.booklist.filter(
                book => book.bookId != bookId
              );
            }
          });
        }
      });
    }
  }
};
</script>
